.about {
  background-color: $white-fa;
  padding: 100px 80px ;
  @include sp {
    padding: 70px 20px;
  }
  
  &__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
  }

  &__title {
    display: block;
    font-size: 4rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-align: center;
    margin-bottom: 35px;
    position: relative;
    @include sp {
      font-size: getsp375(4rem);
      letter-spacing: 1px;
      margin-bottom: 50px;
    }

    &::after {
      content: '';
      position: absolute;
      top: calc(100% + 1.5rem);
      height: 5px;
      width: 30px;
      background: $primary;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 5px;
      @include sp {
        font-size: getsp375(4rem);
        letter-spacing: 1px;
        top: calc(100% + 0.5rem);
      }
    }
  }


  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;
    @include sp {
      grid-template-columns: 1fr;
      grid-gap: 0;
      row-gap: 50px;
    }
  }

  &__content-title {
    font-weight: $bold;
    font-size: 2.8rem;
    margin-bottom: 30px;
    @include sp {
      font-size: getsp375(3.8rem);
      margin-bottom: 10px;
    }
  }

  &__content-knowme  {
    @include sp {
      grid-column: 1 / span 1;
      grid-row: 2/ 2 ;
    }
  }

  &__content-img-wrap {
    @include sp {
      grid-column: 1 / span 2;
      grid-row: 1 /span 1;      
    }
  }

  &__content-knowme-text {
    font-size: 1.8rem;
    color: #666;
    max-width: 600px;
    margin-bottom: 10px;
    @include sp {
      font-size: getsp375(2.8rem);
      line-height: 17px;
    }
  }

  &__content-skill-wrap {
    display: flex;
    flex-wrap: wrap;
  }

  &__content-skill-item {
    padding: 10px 20px;
    margin: 0 15px 15px 0;
    font-size: 1.6rem;
    background: rgba(153,153,153,.2);
    border-radius: 5px;
    font-weight: $semibold;
    color: $subtext;
    transition: all 0.3s;
    @include sp {
      font-size: getsp375(2.6rem);
      padding: 10px 20px;
      margin: 0 8px 8px 0;
      padding: 8px 14px;
    }

    &:hover {
      transform: scale(0.95);
    }
  }

  &__content-knowme-btn-wrap {
    margin-top: 50px;
    @include sp {
      margin-top: 20px;
    }
  }

  &__content-img {
    width: 100%;
  }
}
