.socials {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  background: $white;
  box-shadow: rgb(100 100 111 / 20%) 0 7px 29px 0;
  padding: 5px;
  z-index: 1;
  @include sp{
    display: none;
  }

  &__link-wrap {
    width: 50px;
  }

  &__link {
    width: 100%;
    display: block;
    padding: 10px;
    transition: all .3s;
    border-radius: 5px;

    &:hover {
      background: $primary;
    }
  }

  &__link-icon {
    width: 30px;
    height: 30px;
  }
}
