.home {
  background-image: url('../img/pexels-gdtography-950241.jpg');
  background-repeat: no-repeat;
  background-position: top;
  background-size: auto;

  &__container {
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    max-width: 1200px;
    min-height: 70px;
    max-height: 1200px;
    position: relative;
    @include sp {
      min-height: unset;
      max-height: 700px;
      height: 580px;
    }
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    width: 100%;
    padding: 10px 50px;
    max-width: 980px;
    @include sp {
      padding: 10px 20px;
      top: 55%;
    }
  }

  &__title {
    font-size: 6rem;
    letter-spacing: 3px;
    font-weight: $bold ;
    @include sp {
      font-size: getsp375(7rem);
      letter-spacing: 1px;
    }
  }

  &__text {
    font-size: 2.2rem;
    padding: 0 50px;
    margin: 30px auto 0 auto;
    @include sp {
      font-size: getsp375(3.2rem);
      padding: 0 20px;
      margin: 15px 0 0 0;
    }  
  }

  &__btn-wrap {
    margin-top: 50px;
    @include sp {
      margin-top: 20px;
    }  
  }

  &__mouse-wrap {
    position: absolute;
    bottom: 3%;
    left: 50%;
    transform: translateX(-50%);
  }

  &__mouse {
    width: 25px;
    height: 40px;
    border: 2px solid $grey-33;
    border-radius: 60px;
    position: relative;
    overflow: hidden;

    &::after {
    content: '';
    width: 5px;
    height: 5px;
    position: absolute;
    top: 7px;
    left: 50%;
    transform: translateX(-50%);
    background-color: $grey-33;
    border-radius: 50%;
    opacity: 1;
    animation: drop 1.3s infinite;
    }

    @keyframes drop {
      0% {
        top: 5px;
        opacity: 0;
      }
      30% {
        top: 10px;
        opacity: 1;
      }
      100% {
        top: 25px;
        opacity: 0;
      }
    }
  }
}

