.header {
  width: 100%;
  background-color: $white;
  position: fixed;
  z-index: 1;
  box-shadow: 0 10px 100px rgb(0 0 0 / 10%);

  &__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    @include sp {
      padding: 10px 20px;
    }
  }

  &__logo-name {
    font-size: 2rem;
    letter-spacing: 1px;
    font-weight: $bold;
    @include sp {
      font-size: getsp375(3rem);
    }
  }

  &__logo-title {
    color: $subtext;
    font-size: 1.6rem;
    @include sp {
      font-size: getsp375(2.6rem);
    }
  }

  &__links {
    display: flex;
    @include sp {
      display: none;
    }
  }

  &__link {
    font-size: 1.6rem;
    padding: 22px 30px;
    font-weight: $bold;
    transition: color .3s;

    &:hover {
      color: $primary;
    }
  }

  &__link-sp {
   font-size: 1.4rem; 
   font-weight: $bold;
   display: block;
   padding: 20px 20px;
   text-align: center;
   border-top: 1px solid $grey-ee;
  }

  &__links-sp {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    width: 100%;
    top: 100%;
    transition: all 0.3s;
  }

  .active {
    @include sp {
    position: relative;
    display: block;
    visibility: visible;
    opacity: 1;      
    }
  }
}
