.btn {
  background: $primary;
  color: $white;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: inline-block;
  font-weight: $bold;
  border-radius: 5px;
  box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
  border: 1px solid $primary;
  transition: transform .3s;
  

  &:hover {
    transform: translateY(-3px);
    color: $primary;
    background: $white-fa;
  }

  &--home {
    padding: 15px 80px;
    font-size: 2rem;
    @include sp {
      padding: 10px 40px;
      font-size: getsp375(3rem);
    }
  }

  &--about {
    padding: 15px 50px;
    font-size: 1.6rem;
    @include sp {
      padding: 10px 30px;
      font-size: getsp375(2.6rem);
    }
  }

  &--submit {
    padding: 15px 50px;
    font-size: 1.6rem;
    cursor: pointer;
    @include sp {
      padding: 10px 30px;
      font-size: getsp375(2.6rem);
    }
  }

}
