.footer {
  background-color: $black-01;
  color: $white;
  padding: 0 80px;
  @include sp {
    padding: 0 20px;
  }

  &__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
    padding: 50px 0 40px 0;
    @include sp {
      padding: 30px 0 ;
    }
  }

  &__social{
    @include sp {
      padding-top: 20px;
    }
  }

  &__column {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
    border-bottom: 1px solid $white;
    @include sp {
      flex-direction: column;
      padding-bottom: 20px;
    }
  }

  &__copyright  {
    text-align: center;
    padding-top: 40px;
    font-size: 1.4rem;
    @include sp {
      font-size: getsp375(2.4rem);
      padding-top: 30px;
    }
  }

  &__title {
    font-size: 2.2rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    @include sp {
      font-size: getsp375(3.2rem);
    }
  }

  &__subtitle {
    font-size: 1.6rem;
    letter-spacing: 1px;
    color: $subtext;
    @include sp {
      font-size: getsp375(2.6rem);
    }
  }

  &__social-link {
    margin: 20px 15px 0 0 ;
    @include sp {
      margin: 10px 0 0 0 ;
    }
  }

  &__social-icon {
    margin: 20px 15px 0 0;
    @include sp {
      margin: 10px 5px 0 0;
    }
  }
}
