.contact {
  background-image: url('../img/pexels-gdtography-950241.jpg');
  background-repeat: no-repeat;
  background-position: top;
  background-size: auto;

  &__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
    padding: 100px 0 ;
    @include sp {
      padding: 70px 0;
    }
  }

  &__title {
    display: block;
    font-size: 4rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-align: center;
    margin-bottom: 35px;
    position: relative;
    @include sp {
      font-size: getsp375(4rem);
      letter-spacing: 1px;
      margin-bottom: 50px;
    }

    &::after {
    content: '';
    position: absolute;
    top: calc(100% + 1.5rem);
    height: 5px;
    width: 3rem;
    background: $primary;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
    }
  }

  &__form-wrap {
    box-shadow: rgb(100 100 111 / 20%) 0 7px 29px 0;
    background: $white;
    padding: 40px;
    max-width: 800px;
    text-align: left;
    width: 95%;
    border-radius: 10px;
    margin: 50px auto 0 auto;
    @include sp {
      padding: 20px;
    }
  }

  &__form-input {
    color: $subtext;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebebeb;
    font-size: 1.6rem;
    letter-spacing: 0;
    background: #f0f0f0;
    border-radius: 5px;
    font-weight: $semibold;
    @include sp {
      font-size: getsp375(3rem);
      padding: 1.5rem;
    }

    &:focus {
      border: 1px solid $black;
    }
  }

  &__form-label {
    font-size: 1.4rem;
    letter-spacing: 1px;
    font-weight: $bold;
    margin-bottom: 1rem;
    display: block;
    text-align: left;
    @include sp {
      font-size: getsp375(2.4rem);
      margin-bottom: 5px;
    }
  }

  &__form-field {
    margin-bottom: 30px;
    text-align: center;
  }

  textarea{ 
    resize: none;
    font-family: 'Poppins', sans-serif;
  }

}
