.project {
  padding: 0 80px;
  @include sp {
    padding: 20px  20px;
  }
  
  &__container {
    width: 100%;
    margin: 0 auto;
    max-width: 1200px;
    padding: 100px 0 ;
    @include sp {
      padding: 50px 0 ;
    }
  }

  &__title {
    display: block;
    font-size: 4rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-align: center;
    margin-bottom: 3.5rem;
    position: relative;
    @include sp {
      font-size: getsp375(5rem);
      letter-spacing: 1px;
    }

    &::after {
      content: '';
      position: absolute;
      top: calc(100% + 1.5rem);
      height: 5px;
      width: 3rem;
      background: $primary;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 5px;
      @include sp {
        top: calc(100% + 0.5rem);
      }
    }
  }

  &__content  {
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    gap: 50px;
    margin: 100px 0;
    @include sp {
      grid-template-columns: 1fr;
      margin: 50px 0;
      gap: 30px;
    }
  }

  &__content-item {
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    overflow: hidden;
    position: relative;
  }

  &__content-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 2s cubic-bezier(0,0,.2,1);
    
    &:hover {
      transform: scale(1.25);
    }
  }

  &__content-item-title {
    position: absolute;
    font-size: 2rem;
    top: 20px;
    left: 20px;
    padding: 0 20px;
    background: $black-01;
    color: $white;
    font-weight: bold;
    @include sp {
      font-size: getsp375(3rem);
      top: 15px;
      left: 15px;
    }
  }
}
